export interface Category {
  /**
   * Category ID.
   * @immutable
   * @maxLength 38
   */
  id?: string;
  /**
   * Category label. Displayed in the Category Menu.
   * @maxLength 35
   */
  label?: string;
  /**
   * Number of posts in the category.
   * @readonly
   */
  postCount?: number;
  /**
   * The `url` of the page that lists every post with the specified category.
   * @readonly
   */
  url?: PageUrl;
  /**
   * Category description.
   * @maxLength 500
   */
  description?: string | null;
  /**
   * Category title.
   * @maxLength 200
   * @deprecated Category title.
   * @targetRemovalDate 2025-07-16
   */
  title?: string;
  /**
   * Position of the category in the [Category Menu](https://support.wix.com/en/article/wix-blog-adding-and-customizing-a-category-menu).
   * Categories are displayed in ascending order. Categories with a position of `-1` appear at the end of the sequence.
   *
   * Default: `-1`
   */
  displayPosition?: number | null;
  /**
   * ID of the category's translations. All translations of a single category share the same `translationId`.
   * @format GUID
   */
  translationId?: string | null;
  /**
   * Category language.
   *
   * 2-or-4-letter language code in [IETF BCP 47 language tag](https://en.wikipedia.org/wiki/IETF_language_tag) format.
   * @immutable
   */
  language?: string | null;
  /**
   * Part of a category's URL that refers to a specific category. For example, the slug of `https:/example.com/blog/category/famous-cats` is `famous-cats`.
   * @maxLength 100
   */
  slug?: string;
  /** SEO data. */
  seoData?: SeoSchema;
  /** Category cover image. */
  coverImage?: Image;
  /**
   * Date and time the Category was last updated.
   * @readonly
   */
  updatedDate?: Date | null;
}

export interface PageUrl {
  /** The base URL. For premium sites, this is the domain. For free sites, this is the site URL. For example, `mysite.wixsite.com/mysite`. */
  base?: string;
  /** The relative path for the page within the site. For example, `/product-page/a-product`. */
  path?: string;
}

/**
 * The SEO schema object contains data about different types of meta tags. It makes sure that the information about your page is presented properly to search engines.
 * The search engines use this information for ranking purposes, or to display snippets in the search results.
 * This data will override other sources of tags (for example patterns) and will be included in the <head> section of the HTML document, while not being displayed on the page itself.
 */
export interface SeoSchema {
  /** SEO tag information. */
  tags?: Tag[];
  /** SEO general settings. */
  settings?: Settings;
}

export interface Keyword {
  /** Keyword value. */
  term?: string;
  /** Whether the keyword is the main focus keyword. */
  isMain?: boolean;
  /**
   * The source that added the keyword terms to the SEO settings.
   * @maxLength 1000
   */
  origin?: string | null;
}

export interface Tag {
  /**
   * SEO tag type.
   *
   *
   * Supported values: `title`, `meta`, `script`, `link`.
   */
  type?: string;
  /**
   * A `{"key": "value"}` pair object where each SEO tag property (`"name"`, `"content"`, `"rel"`, `"href"`) contains a value.
   * For example: `{"name": "description", "content": "the description itself"}`.
   */
  props?: Record<string, any> | null;
  /** SEO tag metadata. For example, `{"height": 300, "width": 240}`. */
  meta?: Record<string, any> | null;
  /** SEO tag inner content. For example, `<title> inner content </title>`. */
  children?: string;
  /** Whether the tag is a [custom tag](https://support.wix.com/en/article/adding-additional-meta-tags-to-your-sites-pages). */
  custom?: boolean;
  /** Whether the tag is disabled. If the tag is disabled, people can't find your page when searching for this phrase in search engines. */
  disabled?: boolean;
}

export interface Settings {
  /**
   * Whether the [automatical redirect visits](https://support.wix.com/en/article/customizing-your-pages-seo-settings-in-the-seo-panel) from the old URL to the new one is enabled.
   *
   *
   * Default: `false` (automatical redirect is enabled).
   */
  preventAutoRedirect?: boolean;
  /**
   * User-selected keyword terms for a specific page.
   * @maxSize 5
   */
  keywords?: Keyword[];
}

export interface Image {
  /** WixMedia image ID. */
  id?: string;
  /** Image URL. */
  url?: string;
  /**
   * Original image height.
   * @readonly
   */
  height?: number;
  /**
   * Original image width.
   * @readonly
   */
  width?: number;
  /** Image alt text. */
  altText?: string | null;
  /**
   * Image filename.
   * @readonly
   */
  filename?: string | null;
}

export interface FocalPoint {
  /** X-coordinate of the focal point. */
  x?: number;
  /** Y-coordinate of the focal point. */
  y?: number;
  /** crop by height */
  height?: number | null;
  /** crop by width */
  width?: number | null;
}

export interface CategoryTranslation {
  /**
   * Category ID.
   * @format GUID
   */
  id?: string;
  /**
   * Label displayed in the categories menu on the site.
   * @maxLength 100
   */
  label?: string | null;
  /**
   * Language of the category.
   * @format LANGUAGE_TAG
   */
  language?: string | null;
  /** URL of this category page. */
  url?: PageUrl;
}

export interface InitialCategoriesCopied {
  /** Number of categories copied. */
  count?: number;
}

export interface CreateCategoryRequest {
  /** Category info. */
  category: Category;
  /**
   * List of additional category fields to include in the response. By default
   * only the category’s base fields are returned. Base fields are all category fields that don't appear in the fieldset enum.
   *
   * To retrieve a field, pass the relevant fieldset in the enum in the `fieldsets` array. For example, add the `URL` fieldset to `fieldsets` to
   * retrieve the url field in the response in addition to the category’s base fields.
   * @maxSize 10
   */
  fieldsets?: FieldWithLiterals[];
}

export enum Field {
  UNKNOWN = 'UNKNOWN',
  /** Includes category URL. */
  URL = 'URL',
  /** Includes SEO data. */
  SEO = 'SEO',
}

/** @enumType */
export type FieldWithLiterals = Field | 'UNKNOWN' | 'URL' | 'SEO';

export interface CreateCategoryResponse {
  /** Category info. */
  category?: Category;
}

